import uniLoadMore from '@/components/uLi-load-more/uLi-load-more.vue';
import {
	mapGetters,
	mapMutations
} from "vuex"
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			list: [],
			list1: [{
				name: "综合",
				isTop: true,
				isBottom: false,
				isShow: false
			}, {
				name: "新品",
				isTop: false,
				isBottom: true,
				isShow: false
			}, {
				name: "销量",
				isTop: false,
				isBottom: true,
				isShow: true
			}, {
				name: "价格",
				isTop: false,
				isBottom: true,
				isShow: true
			}],
			scrollLeft: 0,
			sortScrollLeft: 0,
			TabCur: 0,
			isFixd: false,
			themeColor: '#ED494CFF',
			cur: 0,
			factory_id: '',
			factoryInfo: [],
			sort_num: 1, // 排序编号
			type_id: '', // type_id
			pageIndex: 1, // 页码
			loadingType: 'more',
			goodsList: [], // 商品列表
			next_page: '',
			// shopCarCount:'',
			userInfo: []
		}
	},
	computed: {
		goodsListData() {
			return this.goodsList
		},
		...mapGetters({
			getshopNum: 'getshopNum'
		})
	},
	onLoad(options) {
		this.factory_id = uni.getStorageSync('factory_id')
		// console.log(this.factory_id)
		console.log(options)
		if (options.s) {
			uni.setStorageSync('shareStr', options.s)
		} else {
			uni.setStorageSync('shareStr', '')
		}
		if (options.factory_id) {
			this.factory_id = options.factory_id
		}
		this.getFactoryInfo()

		this.userInfo = uni.getStorageSync('user_info')
		this.getShopNum()
	},
	onReachBottom() {
		if (this.loadingType === 'noMore') {
			return
		} else {
			this.getGoods()
		}
	},
	methods: {
		...mapMutations({
			setShopNum: 'setShopNum',
		}),
		toStoreSearch() {
			uni.navigateTo({
				url: '/pages/index/storeHome/storeSearch'
			})
		},
		getFactoryInfo() {
			this.$api.factoryInfo({
				factory_id: this.factory_id
			}).then(res => {
				console.log(res)
				if (res.data.errno === 0) {
					this.factoryInfo = res.data.data
					this.type_id = res.data.data.type_data[0].type_id
					console.log(this.type_id)
					uni.setNavigationBarTitle({
						title: this.factoryInfo.factory_nickname
					})

					this.getGoods()
				}
			})
		},
		// 获取type_id
		tabSelect(e, type_id) {
			console.log(e);
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60;

			// 重新获取对应的数据，且默认为综合
			this.type_id = type_id
			this.sort_num = 1
			this.pageIndex = 1
			this.getGoods()
		},
		// 获得排序编号
		toChange(index) {
			// console.log(index)
			this.cur = index
			this.list1[index].isTop = !this.list1[index].isTop
			this.list1[index].isBottom = !this.list1[index].isBottom;
			switch (index) {
				case 0:
					this.sort_num = 1
					break
				case 1:
					this.sort_num = 2
					break
				case 2:
					if (this.list1[2].isTop) {
						this.sort_num = 4
					} else {
						this.sort_num = 3
					}
					break
				case 3:
					if (this.list1[3].isTop) {
						this.sort_num = 6
					} else {
						this.sort_num = 5
					}
					break
			}
			console.log(this.sort_num)
			this.getGoods()
		},
		toCar() {
			if (this.userInfo.token) {
				uni.switchTab({
					url: '/pages/shopCart/shopCart'
				})
			}else{
				uni.showToast({
					title: '还未登录',
					duration: 1000,
					icon: 'none'
				})
				
				setTimeout(() => {
					uni.switchTab({
						url: '/pages/mine/mine'
					})
				},1000)
			}

		},
		getGoods() {
			if (this.loadingType === 'loading') {
				//防止重复加载
				return;
			}

			this.loadingType = 'loading'

			setTimeout(() => {
				this.$api.factoryGoods({
					factory_id: this.factory_id,
					type_id: this.type_id,
					limit: 10,
					page: this.pageIndex,
					sort_num: this.sort_num,
				}).then(res => {
					console.log(res)
					this.goodsList = res.data.data.list
					// 判断是否有下一页
					if (res.data.data.next_page === 0) {
						this.loadingType = 'noMore'
					} else {
						this.loadingType = 'more'
						this.pageIndex = res.data.data.next_page
					}
				})
			}, 800)
		},
		// 分享按钮点击
		shareFn() {
			uni.showToast({
				title: '还未登录，请先登录',
				duration: 1000,
				icon: 'none'
			})
			setTimeout(res => {
				uni.switchTab({
					url: '/pages/mine/mine'
				})
			}, 1000)
			return false;
		},
		// 获取分享码
		getShareCode() {
			this.$api.shareCode().then(res => {
				if (res.errno == 0) {
					this.shareStr = res.data
				}
			})
		},
		// 跳转到详情页
		toDetail(id) {
			uni.navigateTo({
				url: '/pages/index/shopDetail/shopDetail?id=' + id
			})
		},
		// 获得购物车数量
		getShopNum() {
			if (this.userInfo.token) {
				this.$api.carList().then(res => {
					console.log(res)
					if (res.data.errno == 0) {
						console.log(res.data.data.count)
						this.setShopNum(Number(res.data.data.count))
					} else if (res.data.errno === 400 || res.data.errno === 4008 || res.data.errno === 4009) {
						uni.showToast({
							title: '登录已过时,请重新登录',
							duration: 1000,
							icon: 'none'
						})

						uni.setStorageSync('user_info', {
							token: '',
							user_id: '',
							user_img: '../../static/img/mine/default-img.png',
							user_name: '点击头像登录'
						});

						setTimeout(() => {
							uni.navigateTo({
								url: '/pages/mine/mine'
							})
						}, 1000)
					}
				})
			} else {
				this.shopCarCount = ''
			}
		}
	},
	onShareAppMessage(options) {
		let that = this;
		let shareObj = {
			title: that.factoryInfo.factory_nickname,
			path: '/pages/index/storeHome/storeHome?s=' + that.shareStr + '&factory_id=' + this.factory_id,
		}

		if (options.from == 'button') {
			shareObj.path = '/pages/index/storeHome/storeHome?s=' + that.shareStr + '&factory_id=' + this.factory_id;
		}
		return shareObj;

	},
}
